<template>
  <div class="payment-status" v-cloak>
    <!-- icon-iconfontguanbi failicon-duihao2 bg-color-red-->
    <div
      class="iconfont icon-duihao2 bg-color-red"
      v-if="
        orderInfo.paid == 1 && (orderInfo.status == 1 || orderInfo.status == 2)
      "
    ></div>
    <div class="iconfont icon-iconfontguanbi fail" v-else></div>
    <!--  -->
    <div class="status" v-if="orderInfo.paid == 1 && orderInfo.status == 1">
      
    </div>
    <div
      class="status"
      v-else-if="orderInfo.paid == 1 && orderInfo.status == 0"
    >
      
    </div>
    <div
      class="status"
      v-else-if="orderInfo.paid == 1 && orderInfo.status == 2"
    >
      
    </div>
    <div class="status" v-else></div>
    <div class="wrapper">
      <div class="item acea-row row-between-wrapper">
        <div></div>
        <div class="itemCom" v-text="orderInfo.order_id"></div>
      </div>
      <div class="item acea-row row-between-wrapper">
        <div></div>
        <div class="itemCom" v-text="orderInfo.add_time"></div>
      </div>
      <div class="item acea-row row-between-wrapper">
        <div></div>
        <div
          class="itemCom"
          v-if="orderInfo.pay_type == 'weixin' && orderInfo.is_channel == 2"
        >
          H5
        </div>
        <div
          class="itemCom"
          v-if="orderInfo.pay_type == 'weixin' && orderInfo.is_channel == 0"
        >
          
        </div>
        <div class="itemCom" v-if="orderInfo.pay_type == 'yue'"></div>
        <div class="itemCom" v-if="orderInfo.pay_type == 'offline'">
          
        </div>
      </div>
      <div class="item acea-row row-between-wrapper">
        <div></div>
        <div class="itemCom" v-text="orderInfo.pay_price"></div>
      </div>
      <!--  -->
      <div
        class="item acea-row row-between-wrapper"
        v-if="
          orderInfo.paid == 0 &&
            orderInfo.pay_type != 'offline' &&
            isWeixin &&
            msgContent
        "
      >
        <div></div>
        <div class="itemCom" v-text="msgContent"></div>
      </div>
    </div>
    <!--  -->
    <div v-if="orderInfo.paid == 1 && orderInfo.status == 1">
      <div class="returnBnt bg-color-red" @click="goPages('/user')">
        
      </div>
    </div>
    <div v-else>
      <div class="returnBnt bg-color-red" @click="goPages('/user')">
        
      </div>
      <div class="returnBnt cart-color" @click="goBack()">
        
      </div>
    </div>
    <!-- <div v-else>
      <div class="returnBnt bg-color-red" @click="goPages()"></div>
      <div class="returnBnt cart-color" @click="goBack()"></div>
    </div> -->
  </div>
</template>
<script>
import { mapGetters } from "vuex";
import { orderDetail } from "@api/user";
import { isWeixin } from "@utils";

const NAME = "PayMentStatus";

export default {
  name: NAME,
  props: {},
  data: function() {
    return {
      id: "",
      status: 0,
      msgContent: "",
      orderInfo: {},
      isWeixin: isWeixin()
    };
  },
  watch: {
    $route(n) {
      if (n.query.msg) this.msgContent = n.query.msg;
      if (n.name === NAME && this.id !== n.params.id) {
        this.id = n.params.id;
        this.status = parseInt(n.params.status);
        this.getOrderInfo();
      }
    }
  },
  computed: {
    ...mapGetters(["userInfo"])
  },
  mounted() {
    this.id = this.$route.params.id;
    this.msgContent = this.$route.query.msg;
    this.status = parseInt(this.$route.params.status);
    this.getOrderInfo();
  },
  methods: {
    goPages(route) {
      let routes =
        route !== undefined
          ? route
          : "/order/detail/" + this.orderInfo.order_id;
      if (this.status === 5 && route === undefined) routes = "/order/list/0";
      return this.$router.push({
        path: routes
      });
    },
    goBack() {
      console.log(this.$route.query.id);
      this.$router.replace({
        path: "/packageorder?id=" + this.$route.query.id
      });
    },
    getOrderInfo() {
      orderDetail(this.id).then(res => {
        console.log(res.data);
        this.orderInfo = res.data;
        if (this.isWeixin) {
          document.title = this.orderInfo.paid ? "" : "";
        } else {
          document.title = "";
        }
      });
    }
  }
};
</script>
